<template>
  <div class="" style="padding: 0; margin: 0">
    <svg
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 1440 320"
      class="animated fadeInDown delay-1s"
      style="width: 100%"
    >
      <path
        fill="#0099ff"
        style="width: 100%"
        fill-opacity="1"
        d="M0,192L60,208C120,224,240,256,360,234.7C480,213,600,139,720,128C840,117,960,171,1080,213.3C1200,256,1320,288,1380,304L1440,320L1440,0L1380,0C1320,0,1200,0,1080,0C960,0,840,0,720,0C600,0,480,0,360,0C240,0,120,0,60,0L0,0Z"
      />
    </svg>
    <div class="row justify-content-center animate__animated animate__backInUp">
      <div class="col-md-8">
        <div class="text-center mx-auto">
          <img
            src="@/static/kopdiskominfo.png"
            class="kinoLightBox img-fluid ml-2"
            style="height: 30px"
          />
          
    <div class="sm-form pl-2 pr-2 pt-2">
        <input type="text" id="search" name="search" class="form-control form-control-sm" placeholder="search" v-model="search" >
        <div class="d-none">
        {{skpd}}
        </div>  
          <select class='form-control' style="z-index:100;" v-model="search_skpd">
                  <option value="">-</option>
                  <option v-for="(item, index) in skpd" :key="index+'skpd'" :value="item.id" >{{item.nama_skpd}}</option>
          </select>
    </div>
        </div>
      </div>
      <div class="card-body" style="overflow-x:scroll;">
        <div class="table-responsive-sm">
          <table class="table table-sm table-striped table-hover">
            <!--Table head-->
            <thead class="mdb-color bg-dark">
              <tr class="text-white">
                <th class="text-white">Nama</th>
                <th class="text-white">Nip</th>
                <th class="text-white">Pegawai</th>
                <th class="text-white">Kabid</th>
                <th class="text-white">Pimpinan</th>
              </tr>
            </thead>
            <!--Table head-->
            <!--Table body-->
            <tbody>
              <tr 
               v-for="(item, index) in datas"
                :key="index + 'item'"
                  >
                <td>{{item.nama}}</td>
                <td>{{item.nip}}</td>
                <td>
                    <div v-if="!item.bagian || item.bagian=='-'">
                        <button type="button" class="btn btn-sm btn-primary  ">ON</button> 
                    </div>
                    <div v-else>
                        <button type="button" @click="akses('-',item.id)" class="btn btn-sm btn-dark">ON</button> 
                    </div>
                </td>
                <td>
                    <div v-if="item.bagian=='unit_head'">
                        <button type="button" class="btn btn-sm btn-primary  ">ON</button> 
                    </div>
                    <div v-else>
                        <button type="button" @click="akses('unit_head',item.id)" class="btn btn-sm btn-dark">ON</button> 
                    </div>
                </td>
                <td>
                    <div v-if="item.bagian=='deputy'">
                        <button type="button" class="btn btn-sm btn-primary  ">ON</button> 
                    </div>
                    <div v-else>
                        <button type="button" @click="akses('deputy',item.id)" class="btn btn-sm btn-dark">ON</button> 
                    </div>
                </td>
              </tr>
            </tbody>
            <!--Table body-->
          </table>
          <!--Table-->
        </div>
        <hr class="style13" />
        <!-- <div class="text-center">
              Kembali ke :
                  <button type="submit" @click="$router.push('/')" class="btn btn-style9 "><span class="typcn typcn-key"></span> Login</button>
            </div> -->
      </div>
    </div>
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
      <path
        fill="#0099ff"
        fill-opacity="1"
        d="M0,192L60,170.7C120,149,240,107,360,106.7C480,107,600,149,720,192C840,235,960,277,1080,261.3C1200,245,1320,171,1380,133.3L1440,96L1440,320L1380,320C1320,320,1200,320,1080,320C960,320,840,320,720,320C600,320,480,320,360,320C240,320,120,320,60,320L0,320Z"
      />
    </svg>
  </div>
</template>


<script>
import firebase from "firebase";
let db = firebase.firestore();
import autophp from '@/plugins/autophp.js'
let sdb = new autophp()

export default {
  data() {
    return {
      datanya: [],
      vdata: {
        name: "",
        email: "",
        password: "",
      },
      search:"",
      search_skpd:"",
      error: null,
    };
  },
  computed:{
      datas(){
          let data = this.datanya;
          let that = this;
          data = data.filter(e=>{
            if(e.nama){
                if(e.nama.toLowerCase().indexOf(that.search)!=-1){
                  return e
                }
              }
          })
          data = data.filter((v, i, a) => {
            let hasil = false;
            console.log(v.skpd==that.search_skpd)
              if(v['skpd']==this.search_skpd){
                return v
              }else if(this.search_skpd==''){
                return v
              }
          });
          return data
      }
  },
  methods: {
    akses(datamya,id){
      //idnya letak di vdata
      sdb.collection('tbuser').doc().set({id:id,bagian:datamya}).then(res=>{
          console.log(res)
          this.getData()
      })
    },
    getData(){
    sdb.collection("tbuser").doc().select(`select id,nip,nama,bagian from tbuser where skpd='${this.$route.query.id_skpd}'`).then(res => {
        this.datanya = res;
        this.datanya = _.orderBy(this.datanya, ['nama'],['asc']); 
        this.$forceUpdate()
    });

    },
    test() {
      db.collection("users")
        .doc()
        .then((res) => {
          let data = res.docs.map((e) => {
            return {
              id: e.id,
              ...e.data(),
            };
          });
          console.log(data);
        });
    },
    submit() {
      let that = this;
      firebase
        .auth()
        .createUserWithEmailAndPassword(
          this.vdata.email + "@gmail.com",
          this.vdata.password
        )
        .then((data) => {
          data.user
            .updateProfile({
              displayName: this.vdata.name,
            })
            .then(() => {
              let user = {
                uid: data.user.uid,
                displayName: data.user.displayName,
                email: data.user.email,
                emailVerified: data.user.emailVerified,
                changepassword: "true",
                auth: "user",
                nama: that.vdata.name,
                pertama: "false",
                nip: that.vdata.email,
                changettd: "false",
                emailnotif: "-",
                emails: false,
                fcm: "null",
                fcm2: "null",
                gambar: "-",
                pertama: "false",
                unit: {
                  nama_unit: "BELUM ADA",
                },
              };
              db.collection("users")
                .doc(data.user.uid)
                .set(user, { merge: true })
                .then((res) => {
                  //   console.log("Login Berhasil");
                  alert("REGISTER BERHASIL!");
                  // this.$store.dispatch('logout')
                  localStorage.removeItem("login");
                  // this.$router.push('/')
                  //   this.$router.push("/admin/user");
                });
            });
        })
        .catch((err) => {
          this.error = err.message;
        });
    },
  },
  mounted() {
    this.getData()
    let that =this;
      sdb.collection("skpd").doc().select(`select * from skpd where id_skpd='${this.$route.query.id_skpd}'`).then(res => {
        // console.log(res);
        console.log('skpd',res)
        that.skpd = res
    });
  },
  layout: "admin",
};
</script>